___CSS_LOADER_EXPORT___.push([module.id, "canvas{border:1px solid #9d9c9c!important;border-radius:3px}", "",{"version":3,"sources":["webpack://./resources/js/app/components/SignatureModal.vue"],"names":[],"mappings":"AA6HA,OACI,kCAA+C,CAC/C,iBACJ","sourcesContent":["<template>\n    <div\n        class=\"modal fade show\"\n        tabindex=\"-1\"\n        role=\"dialog\"\n        v-if=\"isVisible\"\n        :class=\"{ 'full-screen': true }\"\n    >\n        <div class=\"modal-dialog\" role=\"document\">\n            <div class=\"modal-content\">\n                <div class=\"modal-header\">\n                    <h5 class=\"modal-title\">ხელმოწერა</h5>\n                    <a\n                        type=\"button\"\n                        class=\"close\"\n                        @click=\"closeAndSave\"\n                        aria-label=\"დახურვა\"\n                    >\n                        <span aria-hidden=\"true\">&times;</span>\n                    </a>\n                </div>\n                <div class=\"modal-body\">\n                    <VueSignaturePad\n                        ref=\"signaturePad\"\n                        class=\"signature-styling\"\n                        :options=\"options\"\n                        width=\"80%\"\n                    ></VueSignaturePad>\n                </div>\n                <div class=\"modal-footer\">\n                    <a class=\"btn btn-primary\" @click=\"saveSignature($event)\">\n                        შენახვა\n                    </a>\n\n                    <a class=\"btn btn-danger\" @click=\"clearSignature\">წაშლა</a>\n                </div>\n            </div>\n        </div>\n    </div>\n</template>\n\n<script>\nexport default {\n    components: {},\n    props: {\n        isVisible: {\n            type: Boolean,\n            default: false,\n        },\n        signatureDataUrl: {\n            type: String,\n            default: \"\",\n        },\n    },\n    watch: {\n        isVisible(newValue) {\n            if (newValue && this.signatureDataUrl) {\n                this.$nextTick(() => {\n                    this.$refs.signaturePad.fromDataURL(this.signatureDataUrl);\n                });\n            }\n        },\n    },\n    data() {\n        return {\n            isFullScreen: false,\n            options: {\n                // Define any signature pad options here\n            },\n            rotationAngle: 90,\n        };\n    },\n    methods: {\n        toggleFullScreen() {\n            this.isFullScreen = !this.isFullScreen;\n        },\n        closeAndSave() {\n            const dataUrl = this.$refs.signaturePad.saveSignature();\n            this.$emit(\"close\", [dataUrl]);\n        },\n        closeModal() {\n            this.$emit(\"close\");\n        },\n        saveSignature(event) {\n            event.preventDefault(); // Prevent the default form submission behavior\n\n            const dataUrl = this.$refs.signaturePad.saveSignature();\n            this.$emit(\"close\", [dataUrl]);\n        },\n        clearSignature() {\n            this.$refs.signaturePad.clearSignature();\n        },\n        rotateCanvas(degrees) {\n            this.rotationAngle = degrees;\n            const canvas = this.$refs.signaturePad.$el.querySelector(\"canvas\");\n            const context = canvas.getContext(\"2d\");\n\n            // Save current image\n            const image = new Image();\n            image.src = canvas.toDataURL();\n\n            image.onload = () => {\n                // Clear the canvas\n                context.clearRect(0, 0, canvas.width, canvas.height);\n\n                // Save the context state\n                context.save();\n\n                // Translate to center of canvas\n                context.translate(canvas.width / 2, canvas.height / 2);\n\n                // Rotate the canvas\n                context.rotate((degrees * Math.PI) / 180);\n\n                // Draw the image in the rotated context\n                context.drawImage(image, -canvas.width / 2, -canvas.height / 2);\n\n                // Restore the context state\n                context.restore();\n            };\n        },\n    },\n};\n</script>\n<style>\ncanvas {\n    border: 1px solid rgb(157, 156, 156) !important;\n    border-radius: 3px;\n}\n</style>\n<style scoped>\n.signature-styling {\n    margin: auto;\n}\n\n.modal.full-screen {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    position: fixed;\n    top: 0;\n    left: 0;\n    width: 100vw;\n    height: 100dvh;\n    overflow: hidden;\n}\n\n.modal.full-screen .modal-dialog {\n    width: 100vw;\n    height: 100dvh;\n    margin: 0;\n    max-width: none;\n}\n\n.modal.full-screen .modal-content {\n    width: 100%;\n    height: 100%;\n    border: none;\n    border-radius: 0;\n}\n\n.modal-body {\n    height: calc(100% - 56px); /* Adjust based on header height */\n    overflow-y: auto;\n}\n\n.rotation-controls {\n    display: flex;\n    justify-content: space-around;\n    margin-top: 10px;\n}\n</style>\n"],"sourceRoot":""}]);
